    import router_build from '../../app_config/router-build'
    import Velocity from 'velocity-animate'
    import Vue from 'vue'

    export default {
        data() {
            return{
                menulist:[],
                showmenu:false,
                list: [
                    { msg: 'Bruce Lee' },
                    { msg: 'Jackie Chan' },
                    { msg: 'Chuck Norris' },
                    { msg: 'Jet Li' },
                    { msg: 'Kung Fury' }
                ],
                query: '',
            }
        },
        mounted(){




        },
        methods:{
            clearquery(){
                this.query = ''
            },
            menu_mouseover(){
                var vm = this
                this.showmenu = true
                Vue.nextTick(function () {
                    vm.menulist = router_build
                })
                $('#slidermenu').css('height','97%')

            },
            menu_mouseleave(){
                this.showmenu = false
                this.menulist = []
                $('#slidermenu').css('height','35px')

            },

            beforeEnter(el) {
                console.log('beforeEnter')
                el.style.opacity = 0
                el.style.height = 0
            },
            enter(el,done) {
                console.log('enter')
                var delay = el.dataset.index * 100
                setTimeout(function () {
                    Velocity(
                        el,
                        { opacity: 1, height: '40px' },
                        { complete: done }
                    )
                }, delay)
            },
            afterEnter(el) {
                console.log('afterenter')

            },
            enterCancelled(el) {
                console.log('enterCancelled')
            },
            beforeLeave(el){
                console.log('befaoreLeave')
            },
            leave(el,done){
                console.log('Leave')
                var delay = el.dataset.index * 100
                setTimeout(function () {
                    Velocity(
                        el,
                        { opacity: 0, height: 0 },
                        { complete: done }
                    )
                }, delay)
            },
            afterLeave(el){
                console.log('afterLeave')
            },
            leaveCancelled(el){
                console.log('leaveCancelled')
            },
            test(){
                alert(1)
            }
        },
        computed:{
            buildMenuList(){
                var vm = this
                var build_menulist = []
                this.menulist.forEach(function (v,k) {
                    v.forEach(function (vv,kk) {
                        build_menulist.push(vv)
                    })
                })
                return build_menulist.filter(function (item) {
                    return item.name.toLowerCase().indexOf(vm.query.toLowerCase()) !== -1
                })
//                return build_menulist
            },

        }

    }



